import React, { Component } from "react"
import PacMan from "../loader/pacMan"
import axios from "axios"
import {
  Line,
  LineChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts"
import { config } from "../../mainConfig"
import dayjs from "dayjs"

class ImportedLineGraph extends Component {
  constructor(props) {
    super(props)
    this.platform = this.props.platform
    this.type = this.props.type
    this.state = {
      data: null,
      loading: true,
      error: null,
    }
    this.getTotalImports = this.getTotalImports.bind(this)
  }

  componentDidMount() {
    this.getTotalImports()

    this.updateMetric = setInterval(this.getTotalImports, config.updateInterval)
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric)
  }

  getTotalImports() {
    axios
      .get(`${config.apiUrl}/api/logs/import/total`, {
        params: {
          platform: this.platform,
          type: this.type,
          from: 30,
        },
      })
      .then((response) => {
        let i,
          result_array = [],
          reverse_api_result = {}
        let curr_date = new Date()
        const now = dayjs(curr_date)
        let target_dates_num = now.diff(now.subtract(1, "month"), "day")

        const get_date = (date) => {
          return dayjs(date).format("YYYY-MM-DD")
        }

        response.data.forEach((day_count) => {
          reverse_api_result[day_count.date] = day_count.count
        })

        for (i = target_dates_num; i > 0; --i) {
          result_array.push({
            date: get_date(curr_date),
            count: reverse_api_result[get_date(curr_date)] || 0,
          })
          curr_date.setDate(curr_date.getDate() - 1)
        }

        const reverse_result = result_array.reverse()
        const data = reverse_result.map((data) => {
          return {
            x: dayjs(data.date).format("YYYY-MM-DD"),
            y: parseInt(data.count),
          }
        })

        this.setState({ data: data, loading: false })
      })
      .catch(() => {
        return this.setState({ error: true, loading: false })
      })
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <ResponsiveContainer width="100%" height={this.props.height}>
          <LineChart data={this.state.data}>
            <XAxis dataKey="x" stroke="#222" tick={false} />
            <YAxis dataKey="y" stroke="#222" />
            <Tooltip />
            <Line
              dataKey="y"
              name="Posts"
              stroke={this.props.color}
              strokeWidth={5}
            />
          </LineChart>
        </ResponsiveContainer>
      )
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200">
          An Error Occurred!
        </p>
      )
    } else {
      return <PacMan />
    }
  }
}

export default ImportedLineGraph
